.avatar {
  position: relative;
  border-radius: 50%;
  margin-bottom: 26px;
  min-height: 120px;
  min-width: 120px;
}

.avatar:hover .avatar__image {
  opacity: 0.8;
}

.avatar:hover .avatar__edit {
  opacity: 1;
}

@media screen and (min-width: 760px) {
  .profile__photo {
    margin: 0;
  }
}
